.logo {
  display: inline-block;
  vertical-align: middle;
  &:hover {
    text-decoration: none; }
  // &__img
  //   margin-bottom: 3px
  //   +r(960)
  //     max-width: 180px
  //   +r(360)
  //     max-width: 150px
  &__description {
    display: block;
    font-size: 17px;
    color: $gray;
    @include rob-l;
    @include r(960) {
      display: none; } } }


.logo {
  img.logo__img {
    margin-bottom: 3px;
    @include r(960) {
      max-width: 180px; }
    @include r(360) {
      max-width: 150px; } } }
