.nav {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  @include r(960) {
    display: none;
    overflow-y: auto;
    // max-height: 100vh
    margin-right: -10px;
    padding-left: 20px;
    padding-bottom: 10px;
    &.is-open {
      display: block; } }
  &__item {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @include r(960) {
      width: 100%; }

    &.is-open {
      background: #fff;
      .nav__in {
        display: block; }
      .nav__link_mod {
        color: #000;
        &:after {
          border-color: #000 transparent transparent; } } } }

  &__list > li {
    @include hover {
      background: #fff;
      .nav__link_mod {
        color: #000;
        &:after {
          border-color: #000 transparent transparent; } } } }
  &__col {
    display: inline-block;
    vertical-align: top;
    margin: 0 20px;
    flex: 1 0 auto;
    columns: 140px;
    column-gap: 2em; }
  &__link {
    position: static;
    color: #000;
    font-size: 16px;
    @include rob-r;
    &_mod {
      display: block;
      text-transform: uppercase;
      padding: 15px 5px;
      font-size: 15px;
      color: #fff;
      @include rob-cb;
      @include md {
        padding: 15px 5px; }
      @include r(960) {
        width: 100%;
        text-align: left;
        padding-left: 20px; }
      &:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-left: 5px;
        @include arr(8, 4, #fff, b); } } }
  &__in {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    // width: 500px
    // top: 500px
    background: #fff;
    max-width: 1200px;
    min-width: 150px;
    z-index: 2;
    padding: 0 10px;
    // flex-flow: row nowrap
    // justify-content: center
    // flex: 1 0 auto
    &.is-sm {
      // width: 200px
      @include r(960) {
        width: 100%; }
      .nav__col {
        margin: 0;
        @include r(960) {
          margin: 0 20px; } } }
    @include r(960) {
      position: relative;
      background: #fff;
      // background: transparent
      max-width: 100%;
      left: 0;
      right: 0;
      padding-left: 20px;
      flex-wrap: wrap; } }
  &__drop_main {
    column-gap: 2em;
    padding: 10px 0;
    @include r(960) {
      padding: 0;
      column-count: 2;
      width: 100%; }
    &.is-big {
      .nav__item_last {
        text-align: left; }
      > li {
        width: 50%;
        display: inline-block; } }
    > li {
      -webkit-column-break-inside: avoid;
      page-break-inside: avoid;
      break-inside: avoid;
      width: 100%;
      // +r(960)
 }      //   width: 50%
    .nav__drop {
      li:last-child {
        @include r(960) {
          padding-bottom: 20px; } } } }
  &__drop_secondary {
    &:not(:last-child) {
      margin-bottom: 10px; } }
  &__drop {
    & > .nav__item {
      display: block;
      & > a {
        &:hover {
          color: $green;
          text-decoration: underline; } } } }

  &__item_bold {
    font-size: 17px;
    color: #000;
    font-weight: bold;
    margin-bottom: 4px;
    @include rob-cb;
    @include r(960) {
      padding-bottom: 5px;
      border-bottom: 1px dotted $red; } } }

.nav__item {
  &_last {
    position: relative;
    margin-top: 5px;
    padding-bottom: 5px;
    padding-top: 10px;
    margin-left: -10px;
    margin-right: -10px;
    text-align: center;
    &:hover {
      > a {
        color: $red; } }
    @include r(960) {
      margin-right: 0;
      margin-left: 0;
      padding-top: 0;
      text-align: left;
 }      // display: inline
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0px;
      right: 0px;
      border-top: 1px solid #e5e5e5;
      padding-bottom: 10px;
      @include r(960) {
        display: none; } }
    > a {
      display: inline-block;
      width: 100%;
      text-transform: uppercase;
      color: $red;
      font-size: 15px;
      white-space: nowrap;
      padding: 3px 10px;
      @include rob-cb;
      @include r(960) {
        padding: 0;
        &:hover {
          background: transparent; } }
      &:hover {
        text-decoration: none;
        color: $red;
        background: #f5f5f5; } } } }
