.header {
  position: relative;
  // overflow: hidden
  &.is-logined {
    .links__item_logout {
      display: block; } }
  &__inner {
    @include clr; }
  &__logo {
    float: left;
    padding: 12px 0;
    @include r(1130) {
      float: none;
      text-align: center; }
    @include r(960) {
      padding-bottom: 0; } }
  &__info {
    margin-top: 10px;
    float: right;
    @include r(1130) {
      float: none;
      text-align: center; }
    @include md {
      margin-bottom: 10px; } }
  &__country {
    padding: 10px 20px;
    float: left;
    @include r(1130) {
      padding-left: 0;
      float: none; } }
  &__middle {
    border-top: 1px solid transparent;
    @include r(960) {
      border-top-color: $yellow2; } }
  &__locator {
    float: right;
    @include r(1130) {
      display: block;
      float: none; } }

  &__bottom {
    .inner {
      padding: 0 15px;
      @include r(1130) {
        padding: 0 10px; } } }

  &__logo-sm {
    display: inline-block;
    vertical-align: middle;
    padding: 5px 0 5px 20px;
    @include r(1130) {
      padding-left: 0; }
    @include r(960) {
      display: none; } }
  &__top-btn {
    display: none;
    @include r(960) {
      display: inline-block;
      vertical-align: middle;
      float: left;
      margin-right: -60px;
      button {
        position: relative;
        // z-index: 99999999
        display: inline-block;
        vertical-align: middle;

        &:not(:last-child) {
          margin-right: 10px; } } } }



  &_yellow {
    background: $yellow;
    border-top: 3px solid $yellow2;
    @include r(960) {
      border-top-color: transparent; }
    .logo__description {
      margin: -14px 0 0 60px;
      color: $red;
      //font-size: 15px
 }      //text-transform: uppercase
    .links__text {
      color: $borde; }
    .links__item_cont {
      .links__pic {
        background: url('../assets/img/general/cont.png');
        background-size: cover; } }
    .links__item_marker {
      .links__pic {
        background: url('../assets/img/general/coins.png');
        background-size: contain;
        background-repeat: no-repeat;
        vertical-align: middle;
        margin-bottom: -5px;
        width: 21px;
        height: 18px; } }

    .links__item_basket {
      .links__pic {
        background: url('../assets/img/general/cart2.png');
        background-size: cover; } }
    .links__item_user {
      .links__pic {
        background: url('../assets/img/general/user.png');
        background-size: cover; } }
    .links__item_logout {
      .links__pic {
        background-image: url('../assets/img/general/logout-rf.png');
        background-size: cover; } }
    .header__middle {
      background: $red; }
    .header__bottom {
      background: $borde;
      @include r(960) {
        background: $red; } }
    .burger {
      background: url('../assets/img/general/burger.png'); }
    .mob-search {
      background: url('../assets/img/general/search-mob.png'); }
    .logo-sm {
      display: inline-block;
      vertical-align: middle;
      background: url('../assets/img/general/home-RFL.png');
      width: 30px;
      height: 31px;
      background-size: cover; } }

